// // CUR COMPONENT
import { connect } from 'react-redux';
// import { Link } from 'react-router-dom'
// import { useParams } from 'react-router-dom'
import React, { useEffect, useState } from 'react';
import LadaIcon from './Icon/lada.svg';

// // LANG
// import dataLang from 'lang/PartsList/Sidebar/lang.json'

interface Data {
  ru: {
    value: string;
    img: string;
    text: string;
  };

  eng: {
    value: string;
    img: string;
    text: string;
  };
}

const generateData = (): Data[] => {
  return new Array(20).fill('').map((el, id) => ({
    ru: {
      value: `Двигатель`,
      text: `Text`,
      img: LadaIcon,
    },
    eng: {
      value: `Engine`,
      text: `Text`,
      img: LadaIcon,
    },
  }));
};

type Props = {
  lang: string;
  data: any;
  setFilter(str: string): void;
};

const QuickSearch: React.FC<Props> = ({ lang, data, setFilter }) => {
  const QUANTITY_OF_ITEMS = 9;
  const [currentQuantity, setCurrentQuantity] = useState<number>(QUANTITY_OF_ITEMS);
  const onElementClickHandler = (evt: React.MouseEvent<HTMLElement>): void => {
    evt.preventDefault();
    setFilter(evt.currentTarget.id);
  };
  const currentArr = generateData();

  return (
    <>
      <ul className='qs-list'>
        {currentArr.slice(0, currentQuantity - 1).map((el, index) => (
          <li className='qs-item' key={index}>
            <a href='/' className='qs-link' id={el.ru.value} onClick={onElementClickHandler}>
              <div className='qs-img-container'>
                <img className='qs-img' src={el.ru.img} alt={el.ru.text} width='50' height='50' />
              </div>
              <span className='qs-text'>{el.ru.text}</span>
            </a>
          </li>
        ))}
        {currentQuantity < currentArr.length && (
          <li className='qs-item'>
            <button
              className='qs-show-more'
              type='button'
              aria-label='Показать ещё?'
              onClick={() => setCurrentQuantity((prev) => prev + QUANTITY_OF_ITEMS)}>
              Показать ещё?
            </button>
          </li>
        )}
      </ul>
    </>
  );
};

const mapStateToProps = (state) => ({
  lang: state.lang,
  data: state.quicSearchData,
});

export default connect(mapStateToProps, null)(QuickSearch);
