import React from 'react';

type Props = {
  data: any;
};

const SelectOption = (name, key) => {
  return (
    <option key={key} value={name}>
      {name}
    </option>
  );
};

const MobileSelect: React.FC<Props> = ({ data }) => {
  console.log(data);
  return (
    <div>
      <select className='category-select' name='category' id='category'>
        {data.map((el, index) => SelectOption(el.name, index))}
      </select>
    </div>
  );
};

export default MobileSelect;
